export class CustomValidator {
    static bmgfNullCheckValidator(value: any) {
        if (value == "" || value == null || value == undefined) {
            return true;
        }
        else {
            return false;
        }
    }

    static bmgfZeroValueValidator(value: any) {
        if (value == 0 || value == "" || value == null || value == undefined) {
            return true;
        }
        else {
            return false;
        }
    }

    static bmgfMobileNoValidator(value) {
        const MOBILE_REGEXP = /^[0-9]{10}$/;
        if (value != null) {
            if ((value != "" || value != 0) && (!MOBILE_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }

    static bmgfPhoneNoValidator(value) {
        const PHONE_REGEXP = /^[0-9]{10,11}$/;
        if (value != null) {
            if ((value != "") && (!PHONE_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    static bmgfEmailValidator(value) {
        const EMAIL_REGEXP = /\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*\s*$/;
        if (value != null) {
            if ((value != "") && (!EMAIL_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }

    static bmgfNumberValidator(value: any) {
        const NUMBER_REGEXP = /^[0-9]+$/;
        if (value != null) {
            if ((value != "") && (!NUMBER_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    static bmgfAlphaNumericValidator(value: any) {
        const ALPHA_REGEXP = /^[a-zA-Z0-9 ]*$/;
        if (value != null) {
            if ((value != "") && (!ALPHA_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    static bmgfDecimalFormValidator(value: any) {
        const DECIMAL_REGEXP = /^[0-9]+(\.[0-9]{1,4})?$/;
        if (value != null) {
            if ((value != "") && (!DECIMAL_REGEXP.test(value))) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    static bmgfCompareTwoDates(smallerDate, biggerDate) {
        if (smallerDate !== 'Invalid Date' && biggerDate !== 'Invalid Date') {
            if (smallerDate.getFullYear() < biggerDate.getFullYear()) {
                return true;
            } else if (smallerDate.getFullYear() === biggerDate.getFullYear()) {
                if (smallerDate.getMonth() < biggerDate.getMonth()) {
                    return true;
                } else if (smallerDate.getMonth() === biggerDate.getMonth()) {
                    if (smallerDate.getDate() <= biggerDate.getDate()) {
                        return true;
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            }  else {
                return false;
            }
        } else {
            return true;
        }

    }

    static bmgfCompareTwoDatesWithEqual(smallerDate, biggerDate) {         
        if (smallerDate !== 'Invalid Date' && biggerDate !== 'Invalid Date') {
            if (smallerDate.getFullYear() < biggerDate.getFullYear()) {
                return true;
            } else if (smallerDate.getFullYear() === biggerDate.getFullYear()) {
                if (smallerDate.getMonth() < biggerDate.getMonth()) {
                    return true;
                } else if (smallerDate.getMonth() === biggerDate.getMonth()) {
                    if (smallerDate.getDate() < biggerDate.getDate()) {
                        return true;
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }
            }  else {
                return false;
            }
        } else if(smallerDate.getFullYear()== biggerDate.getFullYear() 
                   && smallerDate.getMonth() === biggerDate.getMonth()
                   && smallerDate.getDate() == biggerDate.getDate()){
                       return false;
                   }
          else {
            return true;
        }

    }
}

