
import { Observable, throwError } from "rxjs";
import { HttpHeaders, HttpClient, HttpErrorResponse, } from "@angular/common/http";

import { Injectable } from '@angular/core';
import { catchError, map } from 'rxjs/operators';

@Injectable()
export class HttpCleintExtService {

    constructor(private http: HttpClient) {
    }

    public get<T>(url: string, httpOptions: any): Observable<any> {
        //  httpOptions = this.setRequestOptions(httpOptions);
        // url = this.appendTimeInUrl(url);
        return this.http.get<T>(url, { headers: httpOptions })
        // return this.http.get<T>(url, { headers: httpOptions })
        //     .pipe(map(res => {
        //         return res;
        //     }),
        //         //Added for Error Handling
        //         catchError(this.handleError)
        //     );
    }

    public getList<T>(url: string, httpOptions: any, actionMethodName: string): Observable<any> {
        // httpOptions = this.setRequestOptions(httpOptions);
        //   url = this.appendTimeInUrl(url);
        return this.http.get<Array<T>>(url, httpOptions)
    }


    public async getAsync<T>(url: string, httpOptions: any, actionMethodName: string) {
        //  httpOptions = this.setRequestOptions(httpOptions);
        // url = this.appendTimeInUrl(url);
        return await this.http.get<T>(url, httpOptions).toPromise();
    }


    /**
     * Executes the post request with given uri parameters and header values.
     * @param url: The reqeust URI.
     * @param data: The reqeust object.
     * @param args: The request argument.
     */
    public post<T>(url: string, data: T, httpOptions: any): Observable<any> {
        // Commenting This Line As Header Is already getting Set in requestOption
        // const headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8'});
        // httpOptions = this.setRequestOptions(httpOptions);
        //   url = this.appendTimeInUrl(url);
        // Adding Error Handling
        return this.http.post<T>(url, data, { headers: httpOptions })
        // return this.http.post<T>(url, data, { headers: httpOptions })
        //     .pipe(map(res => {
        //         return res;
        //     }),
        //         catchError(this.handleError)
        //     );
    }

    /**
     * Executes the post request with given uri parameters and header values.
     * @param url: The reqeust URI.
     * @param data: The reqeust object.
     * @param args: The request argument.
     */
    public postFile(url: string, data: any, httpOptions: any, actionMethodName: string): Observable<any> {
        // httpOptions = this.setRequestOptions(httpOptions);
        //  url = this.appendTimeInUrl(url);
        return this.http.post(url, data, httpOptions);
    }

    /**
     * Executes the put request with given uri parameters and header values.
     * @param url: The reqeust URI.
     * @param data: The reqesut object.
     * @param args: The reqesut argument.
     */
    public put<T>(url: string, data: any, httpOptions: any, actionMethodName: string): Observable<any> {
        //httpOptions = this.setRequestOptions(httpOptions);
        // url = this.appendTimeInUrl(url);
        return this.http.put<T>(url, data, httpOptions);
    }

    /**
    * Executes the put request with given uri parameters and header values.
     * @param url: The reqeust URI.
     * @param data: The reqesut object.
     * @param args: The reqesut argument.
     */
    public delete(url: string, httpOptions: any, actionMethodName: string): Observable<any> {
        //httpOptions = this.setRequestOptions(httpOptions);
        //url = this.appendTimeInUrl(url);
        return this.http.delete(url, httpOptions);
    }

    //private appendTimeInUrl(url: string): string {
    // if (url.indexOf('?') > -1) {
    //     return url += '&dt=' + new Date().toTimeString();
    // }
    // else {
    //     return url += '?dt=' + new Date().toTimeString();
    // }
    // return url;
    //}

    // Added for error handling
    private handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.log('An error occurred:', error.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.log(
                `Backend returned code ${error.status}, ` +
                `body was: ${error.error}`);
        }
        // return an observable with a user-facing error message
        return throwError(
            'Something bad happened; please try again later.');
    };
}
