    async bmlfCustomCheckValidationMsg(ScreenName: any, FormJson: any, psType: any) {
        this.mObjModel = FormJson;
        // let data = this.bmgfCustomModelTransform(customCtrl);
        // this.bmlfonAssignCustValues(data);
        // if (!FormGridJson) {
        //     FormGridJson = [];
        // }
        let parrHeaderData = [{ ScreenName: ScreenName, ButtonCaption: psType }];
        // let pobjJData = {
        //     ParameterData: parrHeaderData,
        //     HeaderData: [FormJson],
        //     DetailData: FormGridJson,
        //     Response: [{
        //         "ResponseText": "",
        //         "ErrorCode": ""
        //     }]
        // };
        let pobjJData: any = {
            ParameterData: parrHeaderData
        };
        Object.keys(FormJson).forEach(key => {
            pobjJData[key] = FormJson[key];
        });
        pobjJData.Response = [{
            "ResponseText": "",
            "ErrorCode": ""
        }]
        let pobjJson = {
            JSON: JSON.stringify(pobjJData)
        };
        var jsonData = []
        return new Promise((resolve, reject) => {
            this.userFieldService.bmlfCheckValidationEvent(pobjJson).subscribe(res => {
                if (Object.keys(res).length === 0) {
                    resolve(false);
                    return;
                } else if (res.Response[0].ResponseText == undefined) {
                    resolve(false);
                    return;
                } else if (res.Response[0].ErrorCode == "5001" || res.Response[0].ErrorCode == 5001) {
                    jsonData = res.Response
                    resolve(jsonData[0])
                    return;
                } else {

                    if (res.Response[0].ResponseText) {
                        jsonData = res.Response[0].ResponseText.split("|");
                        resolve(jsonData)
                    } else {
                        resolve(false);
                    }
                }
            });
        })
    }

