  public bmlfRunMultipleAPI(reqdata, apiName): Observable<any[]> {
    const url = `${this.appSettings.ServerAPIEndpoint + apiName}`;
    let responseList = [];
    if (reqdata.length) {
      reqdata.forEach((data) => {
        let pobjData = this.httpExt.post<any>(url, data, null);
        responseList.push(pobjData);
      });
    }

    // Observable.forkJoin (RxJS 5) changes to just forkJoin() in RxJS 6
    return forkJoin(responseList);
  }
