import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { locale as navigationEnglish } from '../app/shared/i18n/en';
import { locale as navigationHindi } from '../app/shared/i18n/hi';
import { locale as navigationSpanish } from '../app/shared/i18n/es';
import { locale as navigationVietnamese } from '../app/shared/i18n/vi';
import { locale as navigationPortuguese } from '../app/shared/i18n/pt';
import { locale as navigationChinese } from '../app/shared/i18n/zh';
import { locale as navigationFrench } from '../app/shared/i18n/fr';

import { locale as coreEnglish } from '../app/core/i18n/en';
import { locale as coreHindi } from '../app/core/i18n/hi';
import { locale as coreSpanish } from '../app/core/i18n/es';
import { locale as coreVietnamese } from '../app/core/i18n/vi';
import { locale as corePortuguese } from '../app/core/i18n/pt';
import { locale as coreChinese } from '../app/core/i18n/zh';
import { locale as coreFrench } from '../app/core/i18n/fr';

import { LanTranslationLoaderService } from './core/services/translation-loader.service';
import { AppSettingsService } from './core/services/appsettings.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  title = 'BMWCPoc8';

  constructor(
    private _translateService: TranslateService,
    private _lanTranslationLoaderService: LanTranslationLoaderService,
    private appSettingsService:AppSettingsService
  ) {

    // 
    this.appSettingsService.configure();

    // Add languages
    this._translateService.addLangs(['en', 'hi', 'vi', 'pt', 'es', 'fr', 'zh']);

    // Set the default language
    this._translateService.setDefaultLang('en');

    // Set the navigation translations
    this._lanTranslationLoaderService.loadTranslations(navigationEnglish,navigationHindi,navigationSpanish,navigationVietnamese,navigationPortuguese,navigationChinese,navigationFrench);
    this._lanTranslationLoaderService.loadTranslations(coreEnglish,coreHindi,coreSpanish,coreVietnamese,corePortuguese,coreChinese,coreFrench);

    // Use a language
    this._translateService.use('en');  
  }
}
