import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { TranslateModule } from '@ngx-translate/core';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CoreModule } from './core/core.module';
// import { GridModule } from '@progress/kendo-angular-grid';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NotificationModule } from '@progress/kendo-angular-notification';
// import { ChartsModule } from '@progress/kendo-angular-charts';
import 'hammerjs';
import { HttpCleintExtService } from './core/http/http-client-ext.service';
import { LoginComponent } from './account/components/login/login.component';
import { FormsModule } from '@angular/forms';
import { UserFieldService } from './shared/http/userfield.service';
import { CommonHeaderInterceptor } from './core/interceptors/commonHeader-interceptor';
import { ToastMessageService } from './core/services/toast-message.service';
import { DialogsModule } from '@progress/kendo-angular-dialog';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { gloabalErrorHandler } from './core/interceptors/globalerrorhandler';
import { StoreModule } from '@ngrx/store';
import { reducers, metaReducers } from './reducers';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { environment } from '../environments/environment';
import { EffectsModule } from '@ngrx/effects';
import { AppEffects } from './app.effects';
import { Globals } from './Global';
// import { SharedComponentModule } from './shared/modules/shared-component.module';
// import { GanttModule } from "@progress/kendo-angular-gantt";
import { IconsModule } from '@progress/kendo-angular-icons';


/** Http interceptor providers in outside-in order */

export const commonHeaderInterceptorProviders = [
  { provide: HTTP_INTERCEPTORS, useClass: CommonHeaderInterceptor, multi: true },
];
export const globalErrror = [
  {provide: ErrorHandler,useClass:gloabalErrorHandler}
];
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent
  ],
  imports: [
    FormsModule,
    HttpClientModule,
    BrowserModule,
    AppRoutingModule,
    TranslateModule.forRoot(),
    CoreModule,
    // GridModule,
    BrowserAnimationsModule,
    // ChartsModule,
    NotificationModule,
    DialogsModule,
    ButtonsModule,
    // SharedComponentModule,
   // GanttModule,
    //KanbanModule,
    StoreModule.forRoot(reducers, {
      metaReducers,
      runtimeChecks: {
        strictStateImmutability: true,
        strictActionImmutability: true
      }
    }),
    StoreDevtoolsModule.instrument({ maxAge: 25, logOnly: environment.production }),
    EffectsModule.forRoot([AppEffects]),
    IconsModule
  ],
  providers: [
    HttpCleintExtService,
    commonHeaderInterceptorProviders,
    UserFieldService,
    ToastMessageService,
    globalErrror,
    Globals
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
