import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class AppSettingsService {

    appSettingsFilePath = "../../assets/appsettings.json";//"../assets/config.json";

    public ServerAPIEndpoint: string = '';
    public ServerAPIEndpointnew: string = '';
    public DragAndAnalyzeEndPoints: string = '';
    public AppGuiVersion: string = '';
    public socketIoEndPoints: string = '';
    public WhatsNewEndPoints: string = '';
    public AdminUrl: string = '';
    public Sprint: string = '';

    constructor(private httpClient: HttpClient) {
    }

    public configure() {
      this.httpClient.get("assets/apipath/apiurl.txt").subscribe(data => {
        this.ServerAPIEndpoint = data['apiurl'];
        this.ServerAPIEndpointnew = data['apiurl'];
        this.DragAndAnalyzeEndPoints = data["daurl"];
        this.AppGuiVersion = data["appVersion"];
        this.socketIoEndPoints = data["socketUrl"];
        this.WhatsNewEndPoints = data["whatsNew"];
        this.AdminUrl = data["adminUrl"];
      })
    }
}
